.app {
  font-family: sans-serif;
  text-align: center;
  height: 100vh;
  width: 100vw;

  display: grid;
  grid-template-columns: 45% 25% 25%;
  grid-template-rows: 10% 40% 40% 10%;
  grid-gap:10px;
}

.app-mobile {
  font-family: sans-serif;
  text-align: center;
  height: 100vh;
  width: 100vw;
}

body {
  padding: 0;
  margin: 0;
}

#table {
  grid-row: 2/4;
  grid-column: 1;
}

#intro {
  grid-row: 1;
  grid-column: 2/4;
}

#table-mobile {
  margin: 0 auto;
  width: 90vw;
}

#map{
  grid-row: 3;
  grid-column: 3/4;
  max-width: 100%;
  justify-content: end;

}

#objintro{
  grid-row: 3;
  grid-column: 2/3;
  text-align: left;
  padding: 10px;
}

.leaflet-container{
  max-width: 90%;
  max-height: 100%;
  margin: auto;
}

#map-mobile{
  margin: 0 auto;
  width: 90vw;
}

.sketchfab-embed-wrapper {
  grid-row: 2;
  grid-column: 2/4;
}

.sketchfab-embed-wrapper-mobile {
  min-height: 100px;
}

iframe{
  height: 90%;
  width: 90%;
}

#footer{
  grid-row:4;
  grid-column:1/4;
  margin: auto;
}
